.home img {
  position: relative;
  cursor: pointer;
  -webkit-transition-duration: 0.5s;
       -o-transition-duration: 0.5s;
          transition-duration: 0.5s;
}
.home img:hover {
  -webkit-transform: scale(1.1);
      -ms-transform: scale(1.1);
          transform: scale(1.1);
  z-index: 999;
}
.home img:active {
  -webkit-transform: scale(1);
      -ms-transform: scale(1);
          transform: scale(1);
}



